/* App.css */

body {
    font-family: 'Roboto', sans-serif;
    background-color: #f8f9fa;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  
  .App {
    width: 400px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    overflow: hidden;
  }
  
  h1 {
    text-align: center;
    color: #343a40;
    background-color: #28a745;
    margin: 0;
    padding: 20px 0;
    font-size: 24px;
  }
  
  form {
    display: flex;
    flex-direction: column;
    padding: 20px;
  }
  
  input[type="text"] {
    padding: 12px;
    font-size: 16px;
    border: none;
    border-bottom: 2px solid #28a745;
    margin-bottom: 20px;
    outline: none;
    transition: border-bottom 0.3s ease;
  }
  
  input[type="text"]:focus {
    border-bottom: 2px solid #218838;
  }
  
  button {
    background-color: #28a745;
    color: #fff;
    padding: 12px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #218838;
  }
  
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  li {
    border-bottom: 1px solid #dee2e6;
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  li:last-child {
    border-bottom: none;
  }
  
  li button {
    background-color: #dc3545;
    color: #fff;
    padding: 8px 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    margin-left: 8px;
    transition: background-color 0.3s ease;
  }
  
  li button.edit {
    background-color: #007bff;
  }
  
  li button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  li input[type="text"] {
    flex: 1;
    margin-right: 8px;
    padding: 8px;
    font-size: 14px;
    border: 1px solid #ced4da;
    border-radius: 5px;
    outline: none;
  }
  
  /* Add more styles as needed */
  